<template>
<div style="height:100%;">
<div class="main-container">
    <div class="main-div">
    <div class="main-content" style="height:100%;">
            <!-- 分类选择 -->
            <el-cascader class="cate-select for-holder-color select-c" :options="options" size="small" placeholder="全部分类" change-on-select>
            </el-cascader>
            <!-- 状态选择 -->
            <el-cascader class="status-select for-holder-color select-c" :options="options" size="small" placeholder="全部状态" change-on-select>
            </el-cascader>

            <!-- 日期选择 -->
            <el-date-picker
                   size="small"
                   style="margin:11px;float:left;"
                   v-model="value6"
                   type="daterange"
                   range-separator="至"
                   start-placeholder="开始日期"
                   end-placeholder="结束日期">
            </el-date-picker>

            <!-- 搜索框 -->
            <el-input placeholder="请输入名称/编码/规格" class="input-search" size="small" style="float:left;">
            <el-button slot="append" icon="el-icon-search" size="mini"></el-button>
            </el-input>

            <!-- 高级搜索 -->
            <p class="high-search">高级搜索</p>
            <!-- 导出 -->
            <el-select placeholder="导出" v-model="value" class="select-out for-holder-color">
                <el-option
                  v-for="item in options2"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
            </el-select>

            <!-- 表格数据 -->
            <el-table
                  ref="multipleTable"
                  :data="releaseData"
                  :default-sort = "{prop: 'date', order: 'descending'}"
                  tooltip-effect="dark"
                  height="87%"
                  style="width: 100%;"
                  @selection-change="handleSelectionChange">
            <el-table-column
                  type="selection"
                  width="30">
            </el-table-column>
            <el-table-column
                  type="expand"
                  width="30">
            </el-table-column>
            <el-table-column
                  label="图片"
                  width="100">
            <template slot-scope="releaseData">
            <div class="table-div-img">
            <img class="table-img" :src="releaseData.row.img">
            </div>
            <!-- <div style="margin-top:0px;width:50px;height:50px;border:1px solid #ccc;"></div> -->
            </template>
            </el-table-column>
            <el-table-column
                  prop="spuCode"
                  label="SPU"
                  width="120">
            </el-table-column> 
            <el-table-column
                  prop="country"
                  label="发布国家"
                  width="120">
            </el-table-column>
            <el-table-column
                  prop="name"
                  label="产品名称"
                  width="120">
            </el-table-column>
            <el-table-column
                  prop="cateName"
                  label="分类"
                  width="120">
            </el-table-column>
            <el-table-column
                  prop="tempType"
                  label="模板类型"
                  width="120">
            </el-table-column>
            <!-- <el-table-column -->
                  <!-- prop="attrList" -->
                  <!-- label="规格" -->
                  <!-- width="120"> -->
            <!-- </el-table-column> -->
            <el-table-column
                  prop="status"
                  label="状态"
                  show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                  prop="date"
                  sortable
                  label="日期"
                  width="120">
            <template slot-scope="scope">{{ scope.row.date }}</template>
            </el-table-column>
            <el-table-column
                      fixed="right"
                      label=""
                      width="100">
            <template slot-scope="scope">
            <el-dropdown trigger="click">
            <span class="el-dropdown-link">
                <i class="el-icon-more"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-on:click.native="open">修改</el-dropdown-item>
            <el-dropdown-item >删除</el-dropdown-item>
            <el-dropdown-item >上架</el-dropdown-item>
            <el-dropdown-item >投放</el-dropdown-item>
            </el-dropdown-menu>
            </el-dropdown>
            </template>
            </el-table-column>
            </el-table>
            <div class="page-div">
                <el-pagination
                 style="float:right;margin-top:15px;margin-right:5%;"
                 @size-change="handleSizeChange"
                 @current-change="handleCurrentChange"
                 :current-page="currentPage4"
                 :page-sizes="[100, 200, 300, 400]"
                 :page-size="100"
                 layout="jumper, prev, pager, next, sizes, total"
                 :total="400">
                </el-pagination>
    </div>
    </div>
    
</div>
</div>
</div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            options: [{
                value: 'zhinan',
                label: '指南',
                children: [
                {
                    value: 'daohang',
                    label: '导航',
                    children: [{
                        value: 'cexiangdaohang',
                        label: '侧向导航'
                    }, {
                        value: 'dingbudaohang',
                        label: '顶部导航'
                    }]
                }]
            }],
            options2: [{
                value: '选项1',
                label: '商品基础信息'
            }],
            value: '',
            value6: '',
            // 表格
            releaseData: [],
            multipleSelection: [],
            currentPage1: 5,
            currentPage2: 5,
            currentPage3: 5,
            currentPage4: 4,
        };
    },
    methods:{
        open() {
            this.$alert('这是一段内容', '标题名称', {
                confirmButtonText: '确定',
                callback: action => {
                    //this.$message({
                    //    type: 'info',
                    //    message: `action: ${ action }`
                    //});
                }
            })
        },
        test() {
            //axios('http://local.api.com/v1/categories');
            axios.get('http://local.api.com/v1/categories', {
                    headers: {
                        'Access-Token': '111',
                        "Content-Type": "application/x-www-form-urlencoded"
                    },
                })
                .then(function (response) {
                    console.log(response);
                })
            .catch(function (error) {
                console.log(error);
            });
        },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        },
        initTable() {
            let initList = [];
            let url = "/v1/product/releases?page=" + this.pageSize;
            let _this = this;
            this.$axios.get(url).then((res) => {
                _this.totalSize = parseInt(res.data.data.page_total);
                res.data.data.list.forEach((item) => {
                    let temp = {};
                    temp.id = item.id;
                    temp.date = item.create_time;
                    temp.name = item.name;
                    temp.cateName = item.cateName;
                    temp.spuCode = item.spu_code;
                    temp.country = item.country;
                    temp.tempType = item.temp_type;
                    temp.attrList = item.attr_list;
                    temp.status = item.disable;
                    temp.img = item.img_list[0];
                    console.log(item);
                    _this.releaseData.push(temp);
                });
                console.log(_this.releaseData);
            }).catch((err) => {
                console.log(err);
            })
        },
    },
    created() {
        this.initTable();
    }
}
</script>

<style>
/* 字体hover主色 13c19f */
/* hover背景色 eef1f9 */
.main-container{
    background-color:white;
    width:99%;   
    height:100%;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.main-content{
    padding:10px 30px 0 30px;
    height:100%;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.for-holder-color .el-input__inner::-webkit-input-placeholder {
  color: black;
}
.for-holder-color .el-input__inner:-moz-placeholder {
  color: black;
}
.for-holder-color .el-input__inner:-ms-input-placeholder {
  color: black;
}
/* 设置table trhover 字体 */
tr:hover{
    color:#13c19f;
}
.input-search{
    width:260px;float:left;margin:11px;
}
.high-search{
    float:left;margin-top:10px;font-size:14px;line-height:32px;color:#aaa;
}
.select-c{
    color:black;width:120px;float:left;margin:10px;
}
.btn-add{
    float:right;margin:5px;background-color:#13c19f;color:white;border-color:#13c19f; 
}
.page-div{
    width:100%;height:10%;
}
.select-out{
    float:right;margin:5px;width:80px;
}
.p-span{
    text-align:left;float:left;font-size:14px;line-height:20px;color:#96a6b6;
}
.p-span-active{
    color:black;
}
.el-table td{
    padding:3px 0;
}
.table-img{
    width:100%;height:100%;
}
.table-div-img{
    width:50px;height:50px;border:1px solid #ccc;
}
</style>



